import React from 'react';
import $ from 'jquery';

import './home.css';

const mcApiUrl = "https://mcapi.us/server/status?ip=";

export class Server extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      online: 0,
      players: {
        now: 0,
        max: 100
      },
      version: "1.14.3",
    };

    this.render = this.render.bind(this);
    this.onClick = this.onClick.bind(this);
  }

  refreshFromMcApi() {
    $.get(mcApiUrl + this.props.ip).done((data) => {
      let vers = data.server.name.split(' ')[1];

      if(vers === null || vers === undefined || vers === ""){
        vers = "1.14.3" // Fallback to default version
      }

      this.setState({
        online: data.online,
        players: data.players,
        version: vers,
      })
    });

  }

  onClick = () => this.props.onClick(this.props.name);

  componentWillMount() {
    this.refreshFromMcApi()
  }

  render() {
    let statusClasses = "server-status" + (this.state.online ? " online" : " offline");

    if(this.props.disabled) {
      return (
        <div className="row animate">
          <div className="col-xs-4 col-xs-offset-4 col-sm-4 col-sm-offset-0 col-lg-4 col-lg-offset-0">
            <div className={ "box server" + (this.props.selected ? " selected" : "") } onClick={this.onClick}>
              <p className="server-name">
                <img className="server-icon" src={this.props.icon} alt="Server icon"/>
                <span>
                {this.props.name}
              </span>
              </p>
              <p>
                W trakcie budowy - premiera niebawem!
              </p>
            </div>
          </div>
        </div>
      )
    }

    return (
      <div className="row animate">
        <div className="col-xs-4 col-xs-offset-4 col-sm-4 col-sm-offset-0 col-lg-4 col-lg-offset-0">
          <div className={ "box server" + (this.props.selected ? " selected" : "") } onClick={this.onClick}>
            <p className="server-name">
              <img className="server-icon" src={this.props.icon} alt="Server icon"/>
              <span>
                {this.props.name}
              </span>

              <span style={{ color: '#42bff5' }}>
                {this.props.ip}
              </span>
            </p>
            <p>
              <span className={ statusClasses }>
                {this.state.online ? "ONLINE" : "OFFLINE" }
              </span>
              (
              <span>
                <span className="players-online">{ this.state.players.now }</span>
                /
                <span className="players-max">{ this.state.players.max }</span>
              </span>
              )
            </p>
            <div>
              {this.state.version}
            </div>
          </div>
        </div>
      </div>
    )
  }
}