import React from 'react';

import { Server } from './server.js';
import { Servers } from './servers.js';
import { ServerDescription } from './server-description.js';

export class ServerList extends React.Component {
  constructor(props) {
    super(props);

    const defaultServer = Servers.slice()[0];

    this.state = {
      servers: Servers,
      selectedServer: defaultServer
    };

    this.render = this.render.bind(this);
    this.onClick = this.onClick.bind(this);
  }

  onClick(serverName) {
    const selectedServer = Servers.find((s) => s.name === serverName);

    this.setState({
      selectedServer: selectedServer,
    });
  }

  render() {
    console.log(this.state.selectedServer);

    return (
      <div className="col-md-12">
        <div className="row">
          <div className="col-md-3">
            {
              this.state.servers.map(
                (s) => <Server
                  key={s.ip}
                  name={s.name}
                  ip={s.ip}
                  icon={s.icon}
                  disabled={s.disabled}
                  description={s.description}
                  onClick={this.onClick}
                  selected={s === this.state.selectedServer}
                />
              )
            }
          </div>
          <div className="col-md-9">
            <ServerDescription server={this.state.selectedServer} />
          </div>
        </div>
      </div>
    )
  }
}